.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

/* my shit here */
#page{
  background-image: url("./resources/mac.jpg");
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
}
#Desktop{
  position: fixed;
  bottom: 25vh;
  right:25%;
  height: 50vh;
  width: 50%;
  background-color: rgb(3, 125, 123);
}

input{
  font-size: 5vh;
  margin-left:3vh;
}

.Taskbar {
  bottom: 0px;
  height: 5vh;
  width: 100%;
  background-color: rgb(192,192,192);
}
.start-menu{
  position: fixed;
  bottom:5vh;
}


.task_button{
  background-color: rgb(192,192,192);
  border:none;
}

